<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/consumerServices" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>带 * 为必填项</li>
        <li> 推荐上线初期定义好的服务的保证金准入额度，后期发生变更，可能影响已加入该服务的商家</li>
      </cms-prompt>
      <el-form ref="form" :rules="editRules" :model="formData" label-width="210px">
        <el-form-item label="服务名称" prop="serviceName" :error='errorMessage.serviceName'>
          <el-input v-model="formData.serviceName" :autofocus="true" ref="first" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="图标" prop="resourceId" :error='errorMessage.resourceId' class="brands-logo">
          <cms-picture-zone v-model="formData.resourceId" :data='formData.resourcesSpaceData' v-if="status"></cms-picture-zone>
        </el-form-item>
        <el-form-item label="是否启用" prop="isEnable" :error='errorMessage.isEnable'>
          <cms-switch v-model="formData.isEnable"></cms-switch>
          <div class="gray">设置为否，商家端申请消费者服务功能将无法申请该项服务，已加入服务的商家，所关联的商品将无法显示该项服务</div>
        </el-form-item>
        <el-form-item label="保证金" prop="bond" :error='errorMessage.bond'>
          <el-input v-model="formData.bond" maxlength="10"></el-input>
          <div class="gray">设定商家加入该项服务所需缴纳的保证金，变更后的金额比原先高，则已加入的商家暂时无法使用该项服务，需要补足新增保证金额度；反之，则商家不受影响</div>
        </el-form-item>
        <el-form-item label="排序值" prop="sortNum" :error='errorMessage.sortNum'>
          <el-input v-model="formData.sortNum" maxlength="2"></el-input>
        </el-form-item>
        <el-form-item label="服务描述" prop="description">
          <el-input v-model="formData.description" maxlength="150"></el-input>
        </el-form-item>
        <el-form-item label="服务协议" prop="protocolContent">
           <cms-editor v-model="formData.protocolContent" @ready='getEditor'></cms-editor>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update"  v-color >保存</cms-button>
        <cms-button type='rest' @click="reset">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import {
  consumerServicesEdit,
  consumerServicesGet
} from '@/api/setting/consumerServices.js'
var defaultData = {
  'serviceName': '',
  'resourceId': '',
  'isEnable': true,
  'bond': '',
  'sortNum': '',
  'description': '',
  'protocolContent': ''
}
export default {
  name: 'consumerServicesEdit',
  mixins: [form],
  data() {
    return {
      roleList: [],
      formData: Object.assign({}, defaultData),
      editRules: {// 新增规则
        serviceName: [rules.required('服务名称不能为空')],
        resourceId: [rules.required('请上传图标', 'change')],
        isEnable: [rules.required(null, 'blur', 'boolean')],
        bond: [rules.required('保证金不能为空'), rules.number()],
        sortNum: [rules.number()]
      },
      ue: null
    }
  },
  methods: {
    getEditor(ue) {
      this.ue = ue
    },
    getFormData(id) {
      consumerServicesGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
        this.formData.resourceId = res.data.resourcesSpaceData.id
      })
    },
    update() {
      this.updateForm(consumerServicesEdit, this.formData, '/consumerServices/list', 'list')
    },
    reset() { // 重置
      this.getFormData(this.$route.query.id)
      this.$refs.form.resetFields()
      this.destroyed()
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>




